.auth__save-btn {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
  border-radius: 2px;
  height: 50px;
  border: none;
  margin-top: 216px;
}

.auth__save-btn:hover {
  opacity: 0.85;
}

@media (max-width: 767px) {
  .auth__save-btn {
    margin-top: 173px;
    font-size: 16px;
    line-height: 19px;
  }
}
